.page {
  font-family: Manrope, sans-serif;
  scroll-behavior: smooth;
  color: $main-text-color;

  &:has(.page__menu:target) {
    overflow: hidden;
  }
  
  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: #F9F9F9;
    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}
